.experience__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
}

.experience__container > div {
    background: var(--color-backgrnd-variant);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.experience__container > div:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.experience__container > div h3 {
    font-size: 1.6rem;
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-primary);
}

.experience__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 2rem;
}

.experience__details {
    display: flex;
    gap: 1rem;
}

.experience__details-icon {
    margin-top: 8px;
    color: var(--color-primary);
}

.experience__details h4 {
    color: var(--color-black);
    font-size: 1.2rem;
}

.experience__details small {
    color: var(--color-black-light);
    font-size: 1rem;
}

/* MEDIA QUERY FOR TABLETS (MEDIUM) */
@media screen and (max-width: 1024px) {
    .experience__container {
        grid-template-columns: 1fr;
    }

    .experience__container > div {
        width: 80%;
        padding: 2rem;
        margin: 0 auto;
    }

    .experience__container > div h3 {
        font-size: 1.4rem;
    }

    .experience__content {
        padding: 1rem;
    }

    .experience__details-icon {
        margin-top: 13px;
    }

    .experience__details h4 {
        font-size: 1rem;
    }

    .experience__details small {
        font-size: 0.8rem;
    }
}

/* MEDIA QUERY FOR SMARTPHONES (SMALL) */
@media screen and (max-width: 600px) {
    .experience__container {
        gap: 1rem;
    }

    .experience__container > div {
        width: 100%;
        padding: 2rem 1rem;
    }

    .experience__container > div h3 {
        font-size: 1.2rem;
    }

    .experience__content {
        grid-template-columns: 1fr;
        row-gap: 1rem;
    }

    .experience__details h4 {
        font-size: 0.9rem;
    }

    .experience__details small {
        font-size: 0.75rem;
    }
}
