@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap);
header {
    height: 100vh;
    padding-top: 2rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

.header__container h2 {
    color: var(--color-primary);
}

/* CTA */
.cta {
    margin-top: 2rem;
    display: flex;
    grid-gap: 1.2rem;
    gap: 1.2rem;
    justify-content: center;
}

/* HeaderSocials */
.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    position: absolute;
    left: 1rem;
    bottom: 3rem;
}

svg {
    width: 2rem;
    height: 2rem;
}

.header__socials::after {
    margin-top: 0.8rem;
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

/* profile image */
.me {
    background: linear-gradient(var(--color-primary), transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 6.5rem 1.5rem 1.5rem 1.5rem;
}

.me__pic {
    border-radius: 12rem 12rem 0 0;
}

/* scroll down */
.scroll__down {
    position: absolute;
    right: -1rem;
    bottom: 6rem;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    font-weight: 300;
    font-size: 2rem;
}

/* MEDIA QUERY FOR TABLETS (MEDIUM) */
@media screen and (max-width: 1024px) {
    header {
        height: 75vh;
        margin-bottom: 2rem;
    }

    svg {
        width: 1.2rem;
        height: 1.2rem;
    }

    .cc__pic {
        width: 1.2rem;
        height: 1.2rem;
    }

    .scroll__down {
        font-size: 1rem;
    }

    .header__socials::after {
        margin-top: 0.5rem;
    }
}

/* MEDIA QUERY FOR SMARTPHONES (SMALL) */
@media screen and (max-width: 600px) {
    header {
        height: 81vh;
    }

    .header__socials, .scroll__down {
        display: none;
    }

    .btn {
        font-size: 0.85rem;
        line-height: 1.2rem;
    }

    .btn-primary {
        font-size: 0.85rem !important;
        line-height: 1.2rem !important;
    }
}

nav {
    background: rgba(0, 0, 0, 0.3);
    width: -webkit-max-content;
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    border-radius: 3rem;
    -webkit-backdrop-filter: blur(15px);
            backdrop-filter: blur(15px);
}

nav a {
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
}

nav a:hover {
    background: rgba(0, 0, 0, 0.3);
}

nav a.active {
    background: var(--color-primary);
    color: var(--color-backgrnd);
}
.about__container {
    display: grid;
    grid-template-columns: 35% 50%;
    grid-gap: 15%;
    gap: 15%;
}

.about__me {
    width: 80%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(
        45deg, 
        transparent,
        var(--color-primary),
        transparent
    );
    display: grid;
    place-items: center;
}

.about__me-image {
    border-radius: 2rem;
    overflow: hidden;
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
    transition: var(--transition);
}

.about__me-image:hover {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}

.about__cards {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 1.5rem;
    gap: 1.5rem;
}

.about__card {
    background: var(--color-backgrnd-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}

.about__card:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.about__icon {
    color: var(--color-primary);
    width: 2.5rem;
    height: 2.5rem;
    margin-bottom: 1rem;
}

.about__card h5 {
    font-size: 1.2rem;
    color: var(--color-black);
}

.about__card small {
    font-size: 1rem;
    color: var(--color-black-light);
}

.about__content p {
    margin: 2rem 0 2rem;
    font-size: 1.2rem;
    color: var(--color-white-light);
}

/* MEDIA QUERY FOR TABLETS (MEDIUM) */
@media screen and (max-width: 1024px) {
    .about__container {
        grid-template-columns: 1fr;
        grid-gap: 0;
        gap: 0;
    }

    .about__me {
        width: 60%;
        margin: 2rem auto 4rem;
    }

    .about__content {
        text-align: center;
    }

    .about__content p {
        margin: 1rem 0 1.5rem;
        font-size: 1rem;
    }

    .about__card h5 {
        font-size: 1rem;
    }

    .about__card small {
        font-size: 0.8rem;
    }
}

/* MEDIA QUERY FOR SMARTPHONES (SMALL) */
@media screen and (max-width: 600px) {
    .about__me {
        width: 80%;
        margin: 0 auto 3rem;
    }

    .about__icon {
        width: 2rem;
        height: 2rem;
    }

    .about__cards {
        grid-gap: 1rem;
        gap: 1rem;
    }

    .about__card h5 {
        font-size: 0.9rem;
    }

    .about__card small {
        font-size: 0.75rem;
    }

    .about__content {
        text-align: center;
    }

    .about__content p {
        margin: 1.5rem 0;
        font-size: 0.85rem;
    }

    .btn-primary {
        font-size: 0.85rem !important;
        line-height: 1.2rem !important;
    }
}
.experience__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 2rem;
    gap: 2rem;
}

.experience__container > div {
    background: var(--color-backgrnd-variant);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.experience__container > div:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.experience__container > div h3 {
    font-size: 1.6rem;
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-primary);
}

.experience__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-row-gap: 2rem;
    row-gap: 2rem;
}

.experience__details {
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
}

.experience__details-icon {
    margin-top: 8px;
    color: var(--color-primary);
}

.experience__details h4 {
    color: var(--color-black);
    font-size: 1.2rem;
}

.experience__details small {
    color: var(--color-black-light);
    font-size: 1rem;
}

/* MEDIA QUERY FOR TABLETS (MEDIUM) */
@media screen and (max-width: 1024px) {
    .experience__container {
        grid-template-columns: 1fr;
    }

    .experience__container > div {
        width: 80%;
        padding: 2rem;
        margin: 0 auto;
    }

    .experience__container > div h3 {
        font-size: 1.4rem;
    }

    .experience__content {
        padding: 1rem;
    }

    .experience__details-icon {
        margin-top: 13px;
    }

    .experience__details h4 {
        font-size: 1rem;
    }

    .experience__details small {
        font-size: 0.8rem;
    }
}

/* MEDIA QUERY FOR SMARTPHONES (SMALL) */
@media screen and (max-width: 600px) {
    .experience__container {
        grid-gap: 1rem;
        gap: 1rem;
    }

    .experience__container > div {
        width: 100%;
        padding: 2rem 1rem;
    }

    .experience__container > div h3 {
        font-size: 1.2rem;
    }

    .experience__content {
        grid-template-columns: 1fr;
        grid-row-gap: 1rem;
        row-gap: 1rem;
    }

    .experience__details h4 {
        font-size: 0.9rem;
    }

    .experience__details small {
        font-size: 0.75rem;
    }
}

.projects__container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2.5rem;
    gap: 2.5rem;
}

.projects__item {
    background: var(--color-backgrnd-variant);
    padding: 1.2rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.projects__item:hover {
    border-color: var(--color-primary-variant);
    background: transparent;
}

.projects__item-image {
    border-radius: 1.5rem;
    overflow: hidden;
}

.projects__item h3 {
    margin: 1.2rem 0 2rem;
    color: var(--color-black);
    font-size: 1.2rem;
}

.projects__item-cta {
    display:flex; /* in case of 2 buttons */
    grid-gap: 1rem;
    gap: 1rem; /* in case of 2 buttons */
    margin-bottom: 1rem;
}

/* MEDIA QUERY FOR TABLETS (MEDIUM) */
@media screen and (max-width: 1024px) {
    .projects__container {
        grid-template-columns: 1fr 1fr;
        grid-gap: 1.2rem;
        gap: 1.2rem;
    }

    .projects__item h3 {
        font-size: 1rem;
    }
}

/* MEDIA QUERY FOR SMARTPHONES (SMALL) */
@media screen and (max-width: 600px) {
    .projects__container {
        grid-template-columns: 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }

    .projects__item h3 {
        color: var(--color-black);
        font-size: 0.9rem;
    }

    .btn {
        font-size: 0.85rem;
        line-height: 1.2rem;
    }
}

.education__container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 3rem;
    gap: 3rem;
}

.education {
    background: var(--color-backgrnd-variant);
    border-radius: 0 0 2rem 2rem;
    border: 1px solid var(--color-primary);
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    transition: var(--transition);
}

.education:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.education__head {
    background: var(--color-primary);
    padding: 2rem;
    border-radius: 0 0 2rem 2rem;
    box-shadow: 0 2rem 1rem rgba(0, 0, 0, 0.1);
}

.education__head h3 {
    color: var(--color-backgrnd);
    font-size: 1.2rem;
    text-align: center;
}

.education__list {
    padding: 2rem;
}

.education__list li {
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 0.8rem;
    color: var(--color-black-light);
}

.education__list-icon {
    color: var(--color-primary);
    margin-top: 2px;
}

.education__list p {
    margin-top: 8px;
    font-size: 1rem;
}

/* MEDIA QUERY FOR TABLETS (MEDIUM) */
@media screen and (max-width: 1024px) {
    .education__container {
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 2rem;
        gap: 2rem;
    }

    .education {
        height: auto;
    }

    .education__head h3 {
        font-size: 1.4rem;
    }
}

/* MEDIA QUERY FOR SMARTPHONES (SMALL) */
@media screen and (max-width: 600px) {
    .education__container {
        grid-template-columns: 1fr;
        grid-gap: 1.5rem;
        gap: 1.5rem;
    }

    .education {
        height: auto;
    }

    .education__head h3 {
        font-size: 1.2rem;
    }

    .education__list p {
        font-size: 0.9rem;
    }

    .education__list-icon {
        margin-top: 8px;
    }
}

.container.testimonials__container {
    width: 40%;
    padding: 2.5rem;
}

.client__avatar {
    width: 6rem;
    aspect-ration: 1/1;
    overflow: hidden;
    border-radius: 50%;
    margin: 0 auto 1rem;
    border: 0.3rem solid var(--color-primary-variant);
}

.testimonial {
    background: var(--color-backgrnd-variant);
    text-align: center;
    padding: 2rem;
    border-radius: 2rem;
    -webkit-user-select: none;
            user-select: none;
}

.client__review {
    color: var(--color-black-light);
    font-weight: 300;
    display: block;
    width: 90%;
    margin: 0.8rem auto 0;
    font-size: 1rem;
}

.client__name {
    color: var(--color-primary);
}

.swiper-pagination-bullet-active {
