}

/* MEDIA QUERY FOR TABLETS (MEDIUM) */
@media screen and (max-width: 1024px) {
    .container.testimonials__container {
        width: 70%;
    }

    .client__review {
        font-size: 0.9rem;
    }

    .client__name {
