    }
}

/* MEDIA QUERY FOR SMARTPHONES (SMALL) */
@media screen and (max-width: 600px) {
    .container.testimonials__container {
        width: var(--container-width-small);
    }

    .client__review {
        width: var(--container-width-small);
        font-size: 0.8rem;
    }

    .client__avatar {
