        padding: 2rem;
    }

    .footer__logo {
        margin-bottom: 1rem;
    }

    .permalinks {
        margin-bottom: 2rem;
    }

    .footer__socials {
        margin-top: 1rem;
        margin-bottom: 1rem;
    }
