/* MEDIA QUERY FOR SMARTPHONES (SMALL) */
@media screen and (max-width: 600px) {
    .permalinks {
        flex-direction: column;
        grid-gap: 1.5rem;
        gap: 1.5rem;
    }

    .footer__socials {
        margin-bottom: 2.6rem;
    }
}
/* google fonts import */

/* basic for all pages in all browsers */
* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

/* some variables */
:root {
    --color-backgrnd: #60566d ;
    --color-backgrnd-variant: #c0d3f2;
    --color-primary: #ab798d;
    --color-primary-variant: rgba(171, 121, 141, 0.4);
    --color-white: #fff;
    --color-white-light: rgba(255, 255, 255, 0.6);
    --color-black: #000;
    --color-black-light: rgba(0, 0, 0, 0.6);

    --transition: all 400ms ease;

    --container-width-large-device: 75%;
    --container-width-medium-device: 86%;
    --container-width-small-device: 90%;
}

html {
    scroll-behavior: smooth; 
}

/* remove scrollbar?
::-webkit-scrollbar {
    display: none;
} */

body {
    font-family: 'Poppins', sans-serif;
    background: #60566d;
    background: var(--color-backgrnd);
    color: #fff;
    color: var(--color-white);
    /* line-hight: 1.7; */
    /* background-image: url(../src/assets/violett.jpg); */
}

.container {
    width: var(--container-width-large);
    padding: 4rem;
    margin: 0 auto;
}

h1, h2, h3, h4, h5 {
    font-weight: 500;
}

h2 {
    font-size: 5rem;
}

h5 {
    font-size: 2rem;
}

section {
    margin-top: 1rem;
}

section > h2, section > h5 {
    text-align: center;
    color: rgba(255, 255, 255, 0.6);
    color: var(--color-white-light);
}

section > h2 {
    color: #ab798d;
    color: var(--color-primary);
    margin-bottom: 1rem;
}

.text-light {
    color: rgba(255, 255, 255, 0.6);
    color: var(--color-white-light);
}

.text-black-light {
    color: rgba(0, 0, 0, 0.6);
    color: var(--color-black-light);
}

a {
    color: #ab798d;
    color: var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition); 
}

a:hover {
    color: #fff;
    color: var(--color-white);
}

.btn {
    width: -webkit-max-content;
    width: max-content;
    display: inline-block;
    color: #ab798d;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid #ab798d;
    border: 1px solid var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition);
}

.btn:hover {
    background: #fff;
    background: var(--color-white);
    color: #60566d;
    color: var(--color-backgrnd);
    border-color: transparent;
}

.btn-primary {
    background: #ab798d;
    background: var(--color-primary);
    color: #60566d;
    color: var(--color-backgrnd);
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}

/* MEDIA QUERY FOR TABLETS (MEDIUM) */
@media screen and (max-width: 1024px) {
    .container {
        width: var(--container-width-medium);
        padding: 2rem;
    }

    h2 {
        font-size: 3rem;
    }
    
    h5 {
        font-size: 1.2rem;
    }
}

/* MEDIA QUERY FOR SMARTPHONES (SMALL) */
@media screen and (max-width: 600px) {
    .container {
        width: var(--container-width-small);
        padding: 1.5rem;
    }

    h2 {
        font-size: 1.5rem;
    }
    
    h5 {
        font-size: 0.8rem;
    }
}

