.projects__container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2.5rem;
}

.projects__item {
    background: var(--color-backgrnd-variant);
    padding: 1.2rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.projects__item:hover {
    border-color: var(--color-primary-variant);
    background: transparent;
}

.projects__item-image {
    border-radius: 1.5rem;
    overflow: hidden;
}

.projects__item h3 {
    margin: 1.2rem 0 2rem;
    color: var(--color-black);
    font-size: 1.2rem;
}

.projects__item-cta {
    display:flex; /* in case of 2 buttons */
    gap: 1rem; /* in case of 2 buttons */
    margin-bottom: 1rem;
}

/* MEDIA QUERY FOR TABLETS (MEDIUM) */
@media screen and (max-width: 1024px) {
    .projects__container {
        grid-template-columns: 1fr 1fr;
        gap: 1.2rem;
    }

    .projects__item h3 {
        font-size: 1rem;
    }
}

/* MEDIA QUERY FOR SMARTPHONES (SMALL) */
@media screen and (max-width: 600px) {
    .projects__container {
        grid-template-columns: 1fr;
        gap: 1rem;
    }

    .projects__item h3 {
        color: var(--color-black);
        font-size: 0.9rem;
    }

    .btn {
        font-size: 0.85rem;
        line-height: 1.2rem;
    }
}
